"use client";
import { useState, useEffect } from "react";
import { BookOpen } from "lucide-react";

// Import or create a JSON file with Hadiths
import hadithsData from "@/data/hadiths.json"; // each item: {arabic, urdu, english}

export default function QuoteWidget() {
  const [hadith, setHadith] = useState<{ arabic: string; urdu: string; english: string } | null>(null);
  const [expanded, setExpanded] = useState(false);

  useEffect(() => {
    // Calculate day of year
    const now = new Date();
    const start = new Date(now.getFullYear(), 0, 0);
    const diff = now.getTime() - start.getTime();
    const oneDay = 1000 * 60 * 60 * 24;
    const dayOfYear = Math.floor(diff / oneDay);

    // Pick Hadith based on dayOfYear
    const index = dayOfYear % hadithsData.length;
    setHadith(hadithsData[index]);
  }, []);

  return (
    <div className={`fixed right-4 top-24 w-14 ${expanded ? "w-80" : "w-14"} transition-all duration-300`}>
      <div
        className="bg-yellow-50/20 glass backdrop-blur-sm rounded-xl shadow-glow p-3 cursor-pointer"
        onClick={() => setExpanded(!expanded)}
      >
        <div className="flex items-center gap-2">
          <BookOpen className="h-5 w-5 text-yellow-500" />
          {expanded && <span className="font-medium text-yellow-600">Quote of the Day</span>}
        </div>

        {expanded && hadith && (
          <div className="mt-3 space-y-2 text-sm text-gray-700 dark:text-gray-200">
            <p className="font-semibold text-lg text-right">{hadith.arabic}</p>
            <p className="text-right text-gray-600">{hadith.urdu}</p>
            <p className="text-right text-gray-500 italic">{hadith.english}</p>
          </div>
        )}
      </div>
    </div>
  );
}
