"use client";
import { useState, useEffect } from "react";
import { CloudSun } from "lucide-react";

interface WeatherData {
  temp: number;
  desc: string;
  city: string;
}

export default function WeatherWidget() {
  const [expanded, setExpanded] = useState(false);
  const [weather, setWeather] = useState<WeatherData>({
    temp: 0,
    desc: "Loading...",
    city: "Loading..."
  });

  // Free API: Open-Meteo (no API key required)
  const fetchWeather = async () => {
    try {
      // Example coordinates (Karachi, Pakistan) - can be replaced dynamically
      const lat = 24.8607;
      const lon = 67.0011;
      const res = await fetch(
        `https://api.open-meteo.com/v1/forecast?latitude=${lat}&longitude=${lon}&current_weather=true`
      );
      const data = await res.json();

      if (data.current_weather) {
        setWeather({
          temp: data.current_weather.temperature,
          desc: data.current_weather.weathercode
            ? weatherCodeToDesc(data.current_weather.weathercode)
            : "Clear",
          city: "Karachi"
        });
      }
    } catch (err) {
      console.error("Weather fetch error:", err);
      setWeather({ temp: 0, desc: "Error", city: "N/A" });
    }
  };

  useEffect(() => {
    fetchWeather();

    // Auto update every 10 minutes
    const interval = setInterval(fetchWeather, 10 * 60 * 1000);
    return () => clearInterval(interval);
  }, []);

  const weatherCodeToDesc = (code: number) => {
    // Map Open-Meteo weather codes to human-readable descriptions
    const mapping: Record<number, string> = {
      0: "Clear sky",
      1: "Mainly clear",
      2: "Partly cloudy",
      3: "Overcast",
      45: "Fog",
      48: "Depositing rime fog",
      51: "Drizzle: Light",
      53: "Drizzle: Moderate",
      55: "Drizzle: Dense",
      56: "Freezing drizzle: Light",
      57: "Freezing drizzle: Dense",
      61: "Rain: Slight",
      63: "Rain: Moderate",
      65: "Rain: Heavy",
      66: "Freezing Rain: Light",
      67: "Freezing Rain: Heavy",
      71: "Snow fall: Slight",
      73: "Snow fall: Moderate",
      75: "Snow fall: Heavy",
      77: "Snow grains",
      80: "Rain showers: Slight",
      81: "Rain showers: Moderate",
      82: "Rain showers: Violent",
      85: "Snow showers: Slight",
      86: "Snow showers: Heavy",
      95: "Thunderstorm: Slight or moderate",
      96: "Thunderstorm with slight hail",
      99: "Thunderstorm with heavy hail"
    };
    return mapping[code] || "Unknown";
  };

  return (
    <div className="fixed top-24 right-6 z-50 flex flex-col gap-4">
      <div
        onClick={() => setExpanded(!expanded)}
        className="cursor-pointer glass shadow-glow p-3 w-52 transition-all duration-300 hover:scale-105"
      >
        <div className="flex items-center justify-between">
          <span className="font-semibold text-white">Weather</span>
          <CloudSun className="w-5 h-5 text-yellow-300" />
        </div>
        {expanded && (
          <div className="mt-3 text-sm text-white space-y-2">
            <p>üìç City: {weather.city}</p>
            <p>üå° Temperature: {weather.temp}¬∞C</p>
            <p>‚òÅ Condition: {weather.desc}</p>
          </div>
        )}
      </div>
    </div>
  );
}
