// @ts-nocheck
import * as React from "react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Mic, Paperclip, Send, Square, Sun, Music, Search } from "lucide-react";

interface MessageInputProps {
  onSend: (message: string) => void;
  disabled?: boolean;
}

export function MessageInput({ onSend, disabled }: MessageInputProps) {
  const [message, setMessage] = React.useState("");
  const [isRecording, setIsRecording] = React.useState(false);
  const [musicQuery, setMusicQuery] = React.useState("");
  const [musicUrl, setMusicUrl] = React.useState("");
  const [weather, setWeather] = React.useState({ temp: "--", desc: "Loading..." });

  const textareaRef = React.useRef(null);
  const fileInputRef = React.useRef(null);
  const recognitionRef = React.useRef(null);

  /* ---------------- Speech Recognition ---------------- */
  React.useEffect(() => {
    if (typeof window !== "undefined") {
      const SpeechRecognition =
        window.SpeechRecognition || window.webkitSpeechRecognition;

      if (SpeechRecognition) {
        const recognition = new SpeechRecognition();
        recognition.lang = "en-US";

        recognition.onresult = (e) => {
          setMessage(e.results[0][0].transcript);
          setIsRecording(false);
        };

        recognition.onend = () => setIsRecording(false);
        recognitionRef.current = recognition;
      }
    }
  }, []);

  const toggleRecording = () => {
    if (!recognitionRef.current) return alert("Speech not supported");
    if (isRecording) recognitionRef.current.stop();
    else recognitionRef.current.start();
    setIsRecording(!isRecording);
  };

  /* ---------------- Weather (FREE API ROUTE) ---------------- */
  React.useEffect(() => {
    async function fetchWeather() {
      try {
        const res = await fetch("/api/weather");
        const data = await res.json();
        setWeather(data);
      } catch {}
    }
    fetchWeather();
  }, []);

  /* ---------------- FREE Music Search (iTunes API) ---------------- */
  const searchMusic = async () => {
    if (!musicQuery.trim()) return;

    const res = await fetch(
      `https://itunes.apple.com/search?term=${encodeURIComponent(
        musicQuery
      )}&entity=song&limit=1`
    );

    const data = await res.json();

    if (data.results.length > 0) {
      setMusicUrl(data.results[0].previewUrl);
    }
  };

  /* ---------------- File Upload ---------------- */
  const handleFileClick = () => fileInputRef.current?.click();

  /* ---------------- Send Message ---------------- */
  const handleSend = () => {
    if (!message.trim()) return;
    onSend(message);
    setMessage("");
  };

  return (
    <div className="flex flex-col gap-3 p-3 border rounded-2xl glass bg-background/70">
      
      {/* ---------- TOP WIDGETS ---------- */}
      <div className="flex justify-between items-center">

        {/* Weather */}
        <div className="flex items-center gap-2 px-3 py-1 rounded-full shadow-glow bg-blue-500/10">
          <Sun className="text-blue-400" />
          <span className="text-sm">
            {weather.temp}¬∞C | {weather.desc}
          </span>
        </div>

        {/* Music */}
        <div className="flex items-center gap-2 px-3 py-1 rounded-full shadow-glow bg-green-500/10">
          <Music className="text-green-400" />

          <input
            value={musicQuery}
            onChange={(e) => setMusicQuery(e.target.value)}
            placeholder="Search song..."
            className="bg-transparent text-sm outline-none w-24"
          />

          <Button size="icon" variant="ghost" onClick={searchMusic}>
            <Search className="h-4 w-4" />
          </Button>

          {musicUrl && (
            <audio controls autoPlay src={musicUrl} className="h-6 w-28" />
          )}
        </div>
      </div>

      {/* ---------- INPUT AREA ---------- */}
      <div className="flex items-end gap-2">
        <input
          type="file"
          ref={fileInputRef}
          className="hidden"
        />

        <Button size="icon" variant="ghost" onClick={handleFileClick}>
          <Paperclip className="h-4 w-4" />
        </Button>

        <Textarea
          ref={textareaRef}
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          placeholder="Message RGS AI..."
          className="flex-1 resize-none bg-transparent border-0 focus-visible:ring-0"
          rows={1}
          disabled={disabled}
        />

        {message.trim() ? (
          <Button
            size="icon"
            onClick={handleSend}
            className="rounded-full bg-gradient-to-r from-blue-600 to-cyan-500 text-white"
          >
            <Send className="h-4 w-4" />
          </Button>
        ) : (
          <Button
            size="icon"
            variant={isRecording ? "destructive" : "ghost"}
            className={isRecording ? "animate-pulse-soft" : ""}
            onClick={toggleRecording}
          >
            {isRecording ? (
              <Square className="h-4 w-4" />
            ) : (
              <Mic className="h-4 w-4" />
            )}
          </Button>
        )}
      </div>
    </div>
  );
}
